* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  max-width: 1440px;
  margin: 0 auto;
  font-family: "Segoe UI";
}
nav {
  margin-bottom: 3rem;
}
span {
  color: #5db137;
}
main {
  display: flex;
  flex-direction: column;
  margin-bottom: 200px;
}
.main {
  display: grid;
  grid-template-columns: 4fr 1fr;
  gap: 7.5rem;
}
.main-left {
  text-align: center;
}
.main-left .C {
  letter-spacing: 10px;
  margin-bottom: 3rem;
  margin-top: 2rem;
}
.main-left li {
  list-style: none;
  text-align: left;
  margin-bottom: 10px;
}
.main-left input {
  height: 1.875rem;
  width: 31.25rem;
  border: none;
  /* border-bottom: 1px solid black; */
  /* border-left: 1px solid black; */
  /* border-bottom-left-radius: 5px; */
  box-shadow: 1px 1px 1px;
  outline: none;
}
select {
  background-color: rgb(255, 253, 253);
}

.foto {
  border: 1px solid black;
  height: 13.75rem;
  width: 11.25rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  font-size: 0.75rem;
  /* background: rgb(238, 238, 238); */
  text-align: center;
}
.rem,
.family {
  margin-top: 1.875rem;
  margin-bottom: 1.875rem;
}
.family1{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 20px;
}
.family1 button{
  height: 1.5rem;
  width: 1.5rem;
}
.boxes {
  display: flex;
  flex-direction: column;
}
.box {
  display: flex;
  flex-direction: column;
}
.box-left {
  font-style: italic;
  margin-bottom: 10px;
  font-weight: 600;
  font-size: 0.93rem;
}

.box-right input {
  width: 12rem;
  height: 1.875rem;
  padding: 4px;
  border: none;
  box-shadow: 0px 0px 1px;
  outline: none;
}
.box-right > select {
  width: 62.5rem;
  height: 1.875rem;
  padding: 4px;
  margin-bottom: 10px;
  border: none;
  /* border-bottom: 1px solid black; */
  /* border-left: 1px solid black; */
  /* border-bottom-left-radius: 5px; */
  box-shadow: 0px 0px 1px;
  outline: none;
}

.box-right textarea {
  width: 62.5rem;
  padding: 4px;
  margin-bottom: 10px;
  border: none;
  /* border-bottom: 1px solid black; */
  /* border-left: 1px solid black; */
  /* border-bottom-left-radius: 5px; */
  box-shadow: 0px 0px 1px;
  outline: none;
}
