} */
.lang {
  display: flex;
  justify-content: space-between;
  width: 17.62rem;
  margin-bottom: 10px;
}
.lang select {
  height: 1.875rem;
  width: 2.18rem;
}
.lang .tilni-tanla {
  height: 1.875rem;
  width: 9rem;
  font-size: 1rem;
  border: none;
  border-bottom: 1px dashed #ccc;
}

.lang .tilni-tanla option {
  height: 1.875rem;
  width: 11rem;
  font-size: 1rem;
}

.box-right button {
  height: 1.875rem;
  width: 2.18rem;
}
.lang input {
  width: 9rem;
  font-size: 1rem;
}
.jadval,
.input {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.jadval p {
  height: 5rem;
  width: 12.5rem;
  border: 1px solid black;
  font-weight: 700;
  text-align: center;
}
.input input {
  height: 5rem;
  width: 12.5rem;
  border: 1px solid black;
  font-weight: 500;
  text-align: center;
  /* margin-bottom: 1.875rem; */
}
.input textarea {
  height: 5rem;
  width: 12.5rem;
  border: 1px solid black;
  font-weight: 500;
  text-align: center;
  /* margin-bottom: 1.875rem; */
}

.adres,
.mail,
.number,
.date {
  font-weight: 700;
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
}
.adres p,
.mail p,
.number p,
.date p {
  font-weight: 700;
  margin-bottom: 10px;
  font-style: italic;
}
.adres input,
.mail input,
.number input,
.date input {
  width: 31.25rem;
  height: 1.875rem;
  padding: 4px;
  margin-bottom: 10px;
  border: none;
  /* border-bottom: 1px solid black; */
  /* border-left: 1px solid black; */
  /* border-bottom-left-radius: 5px; */
  box-shadow: 0px 0px 1px;
  outline: none;
}
.raqam {
  display: flex;
  align-items: center;
}
.raqam b,
.raqam select {
  height: 2rem;
  width: 2.81rem;
  border: 1px solid #ccc;
  display: flex;
  align-items: center;
  font-weight: normal;
  font-size: 1rem;
}
.raqam input {
  margin-bottom: 0;
  width: 25.7rem;
  font-size: 1rem;
  list-style: none;
}
label {
  cursor: pointer;
  font-size: 40px;
  background-color: #ccc;
  border-radius: 20px;

}



























@media screen and (max-width: 425px) {
  nav {
    margin: 30px;
  }
  main {
    margin: 30px;
  }
  .adres input,
  .mail input,
  .number input,
  .date input {
    width: 31.25rem;
    height: 1.875rem;
  }
  .box-right textarea {
    width: 50rem;
  }
  .box-right > select {
    width: 50rem;
  }
  .box-right input {
    width: 12rem;
  }
  .raqam input {
    width: 25.7rem;
  }
  .raqam {
    width: 50rem;
  }
  .jadval {
    height: 6rem;
    width: 47rem;
  }
  .jadval p {
    height: 8rem;
  }
  .input input {
    height: 5rem;
    width: 9.4rem;
  }
  .input textarea {
    height: 5rem;
    width: 9.4rem;
  }
  .lang input {   
    width: 9rem;
  }
  .lang .tilni-tanla {
    width: 9rem;    
  }
  .foto{
    position: relative;
    height: 12rem;
    width: 10rem;
  }
  .foto img{
    height: 12rem;
    width: 10rem;
  }
}
